.App {
  text-align: center;
}
.App nav{
  display: flex;
  gap: 10px;
  align-items: center;
}
.App nav .spacer{
  flex:1;

}
.App nav a.active{
  background-color: rgb(104, 97, 185);
  color: antiquewhite;
  text-decoration: none;
  padding: 4px 10px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
